.loading {
  width: 100%;
  height: 100vh;
  font-size: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0c041f;
  color: white;
}
.container {
  background-color: #eee;
  margin: 100px;
}
.contentBox {
  display: flex;
  margin: 50px;
  padding: 50px;
}
.detail_title {
  color: black;
  margin-bottom: 20px;
  font-size: 3em;
  margin: 0;
  font-weight: 500;
  margin-bottom: 30px;
}
.coverImg {
  margin-right: 30px;
  max-height: 700px;
}
.detail_text {
  font-size: 1.5em;
  font-weight: 400;
  text-decoration: none;
  line-height: 2;
}
.detail_genres {
  font-size: 1.2em;
  font-weight: 300;
}
.detail_story {
  font-weight: bold;
}
.detail_story_full {
  line-height: 2;
}
.detail_logo {
  text-align: center;
  margin-top: 50px;
}
@media screen and (max-width: 1200px) {
  .contentBox {
    display: flex;
    flex-direction: column;
  }
  .coverImg {
    max-height: 1000px;
    margin-bottom: 30px;
  }
  .detail_title {
    font-size: 2.5em;
  }
}
