.loading {
  width: 100%;
  height: 100vh;
  font-size: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0c041f;
  color: white;
}
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}
.content {
  display: grid;
  grid-template-columns: repeat(2, minmax(100px, 1fr));
  grid-gap: 100px;
  padding: 80px;
}
.logo {
  margin-top: 50px;
  margin-left: 100px;
  text-align: center;
}
@media screen and (max-width: 1350px) {
  .content {
    grid-template-columns: 1fr;
    width: 100%;
  }
}
