.movie {
  background-color: #eee;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  /* display: flex; */
  display: grid;
  grid-template-columns: minmax(250px, 1fr) 5fr;
  grid-gap: 20px;
  text-decoration: none;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie_img {
  position: relative;
  top: 0;
  max-width: 230px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie_title {
  color: black;
  text-decoration: none;
  margin-bottom: 20px;
  font-size: 2em;
  font-weight: 500;
  margin: 0;
  margin-bottom: 20px;
}
.movie_title a {
  text-decoration: none;
  color: black;
}
.movie_text {
  font-size: 1em;
  font-weight: 400;
  text-decoration: none;
  max-width: 90%;
}
